.main {
  border-top: 3px solid @sub-main-color;
}

.center-main {
  display: flex;
  padding: 0 5px;
}

.sidebar {
  width: 100%;
  max-width: 196px;
  
  border-right: 1px dotted @border-color;
}

.about-author {
  padding-top: 29px;
  padding-bottom: 13px;
  
  border-bottom: 1px dotted @border-color;
}

.about-author__title {
  margin-bottom: 8px;
  
  font-family: @main-font;
  font-size: 22px;
  font-weight: normal;
  text-transform: uppercase;
  color: @main-color;
}

.about-author__desc {
  max-width: 175px;
  
  font-family: Arial, sans-serif;
  font-size: 10px;
  line-height: 1.8;
  color: @content-color;
  & span {
    font-weight: bold;
    color: @main-color;
  }
}

// main
.main-content-wr {
  padding-top: 9px;
}

.main-content {
  margin-top: -12px;
  
  background: #fff;
  border-top: 3px solid @sub-main-color;
}

.main-article {
  padding-top: 15px;
  padding-left: 13px;
  
  border-right: 1px dotted @border-color;
  border-bottom: 1px dotted @border-color;
}

.main-article__title {
  margin-bottom: 10px;
  
  font-family: @main-font;
  font-size: 32px;
  text-transform: uppercase;
  font-weight: normal;
  color: @main-color;
}

.main-article__avatar-wr {
  display: flex;
  margin-right: 27px;
}

.main-article-wr {
  display: flex;
}

.avatar-author,
.avatar-date {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  
  font-family: Arial, sans-serif;
  font-size: 8px;
  text-align: center;
  color: #ffffff;
  
  background: @sub-main-color;
  border-radius: 50%;
}

.avatar-author {
  margin-left: 16px;
  
  background: @sub-main-color url('../img/user-male-icon.png') center 3px no-repeat;
}

.main-article__content {
  margin-bottom: 26px;
  
  font-family: Arial, sans-serif;
  font-size: 10px;
  line-height: 1.8;
  color: @content-color;
}

.main-article__content-row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.main-article__content-link {
  display: block;
  padding: 0 11px;
  
  
  font-family: Arial, sans-serif;
  font-size: 10px;
  font-weight: bold;
  line-height: 2.7;
  color: #fff;
  text-decoration: none;
  
  
  border: 1px solid #045279;
  //background-blend-mode: overlay;
  background-image: linear-gradient(to bottom, #076e9f, #076e9f), linear-gradient(to top, #000000, #ffffff);
}

.main-article__content-link-wr {
  border-bottom:3px solid #00141e;
  box-shadow: 0 2px 0 0 rgba(0, 0, 0, 0.16);
}

.main-article__content-like-wr {
  position: relative;
  
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 180px;
  margin-right: -6px;
  padding: 5px 7px 3px 13px;
  
  font-family: Arial, sans-serif;
  font-size: 10px;
  color: @main-color;
  
  background: #90d4f5;
}

.main-article__content-like-wr::after {
  content: '';
  position: absolute;
  bottom: -5px;
  right: 0;
  
  display: block;
  width: 0;
  height: 0;
  
  border-style: solid;
  border-width: 5px 5px 0 0;
  border-color: #007bff transparent transparent transparent;
  
}

.main-article__like,
.main-article__comment {
  
  padding-left: 26px;
  
  line-height: 1.8;
}

.main-article__like {
  background: url('../img/thumb-up.png') 0 0 no-repeat;
}

.main-article__comment {
  background: url('../img/speech-bubbles.png') 0 1px no-repeat;
}


